import {Sanscript} from "./sanscript.js";
"use strict";
const to = {
  smush: function(text, placeholder) {
    return text.toLowerCase().replace(/([с╕Нdrmvynhs]) ([a─Бi─лu┼лс╣Ыeo├й├│])/g, "$1$2" + placeholder).replace(/([kgcjс╣нс╕Нtdpb]) h/g, "$1тАМ" + placeholder + "h").replace(/([kgcj├▒с╕Нtdnpbmrlyv┼Ыс╣гsс╣Щ]) ([kgcjс╣нс╕Нtdnpbmyrlv┼Ыс╣гshс╕╗])/g, "$1" + placeholder + "$2").replace(/([o├│e├й─Б]) ([с╣Г'])/g, "$1" + placeholder + "$2").replace(/^с╣Г/, "'тАНс╣Г").replace(/^с╕е/, "'тАНс╕е").replace(/^_y/, "'тАНy").replace(/├╝/g, "тАМu").replace(/├п/g, "тАМi").replace(/_{1,2}(?=\s*)/g, function(match) {
      if (match === "__")
        return "тАН";
      else if (match === "_")
        return "тАМ";
    });
  },
  iast: function(text, f) {
    const from = f || "devanagari";
    return Sanscript.t(text, from, "iast", {skip_sgml: true});
  },
  devanagari: function(text, p) {
    const placeholder = p || "";
    const options = {skip_sgml: true};
    const presmush = text.replace(/с╣Щ/g, "r").replace(/^_─Б/, "рд╜тАНрд╛");
    const smushed = to.smush(presmush, placeholder);
    const in_deva = Sanscript.t(smushed, "iast", "devanagari", options);
    return in_deva.replace(/┬п/g, "ъг╗");
  },
  malayalam: function(text, p) {
    const placeholder = p || "";
    const options = {skip_sgml: true};
    const chillu = {
      р┤Х: "р╡┐",
      р┤д: "р╡╜",
      р┤и: "р╡╗",
      р┤о: "р╡Ф",
      р┤░: "р╡╝"
    };
    const presmush = text.replace(/^_─Б/, "р┤╜тАНр┤╛");
    const smushed = to.smush(presmush, placeholder).replace(/e/g, "с║╜").replace(/o/g, "├╡").replace(/с╣Щ/g, "r").replace(/с╣Б/g, "с╣Г").replace(/с╕┐/g, "с╣Г").replace(/├н/g, "i").replace(/├║/g, "u").replace(/├й/g, "e");
    const in_mlym = Sanscript.t(smushed, "iast", "malayalam", options);
    return in_mlym.replace(/(^|[^р╡Н])р┤░р╡Н(?=\S)/g, "$1р╡О").replace(/([р┤Хр┤др┤ир┤ор┤░])р╡Н(?![^\s\u200C,тАФтАЩтАХ])/g, function(match, p1) {
      return chillu[p1];
    });
  },
  grantha: function(text, p) {
    const placeholder = p || "";
    const options = {skip_sgml: true};
    const finals = new Map([
      ["р┤╕", "юЗУ"],
      ["р┤░", "р╡╝"],
      ["р┤п", "юЗЛ"],
      ["р┤Ъ", "юЖ╢"],
      ["р┤ж", "юЗВ"],
      ["р┤╢", "юЗТ"]
    ]);
    const finals_regex = new RegExp("([" + [...finals.keys()].join("") + "])р╡Н(?![^тАМ ,тАФтАЩтАХ])", "g");
    const presmush = text.replace(/^_─Б/, "р┤╜тАНр┤╛");
    const smushed = to.smush(presmush, placeholder).replace(/e/g, "с║╜").replace(/o/g, "├╡").replace(/с╣Щ/g, "r").replace(/с╣Б/g, "с╣Г").replace(/с╕┐/g, "с╣Г").replace(/├н/g, "i").replace(/├║/g, "u").replace(/├й/g, "e");
    const in_gnth = Sanscript.t(smushed, "iast", "malayalam", options);
    return in_gnth.replace(/(^|[^р╡Н])р┤░р╡Н(?=\S)/g, "$1р╡О").replace(finals_regex, (match, p1) => finals.get(p1)).replace(/р┤Хр╡Нр┤╖р┤┐/g, "роХрпНро╖ро┐");
  },
  telugu: function(text, p) {
    var placeholder = p || "";
    const options = {skip_sgml: true};
    const presmush = text.replace(/^_─Б/, "р░╜тАНр░╛");
    const smushed = to.smush(presmush, placeholder).replace(/e/g, "с║╜").replace(/o/g, "├╡").replace(/с╣Щ/g, "rтАН").replace(/с╣Б/g, "с╣Г").replace(/с╕┐/g, "с╣Г").replace(/├н/g, "i").replace(/├║/g, "u").replace(/├й/g, "e");
    return Sanscript.t(smushed, "iast", "telugu", options);
  }
};
const changeScript = function(orignode, script, placeholder = false, cur_lang = "sa") {
  const func = to[script];
  const node = orignode.cloneNode(true);
  const loop = function(node2, cur_lang2) {
    let kids = node2.childNodes;
    for (let kid of kids) {
      if (kid.nodeType === 8)
        continue;
      if (kid.nodeType === 3) {
        if (cur_lang2 !== "sa")
          continue;
        else
          kid.data = func(kid.data, placeholder);
      } else if (kid.hasChildNodes()) {
        let kidlang = kid.getAttribute("lang") || cur_lang2;
        if (kidlang === "sa" && kid.classList.contains("subst"))
          jiggle(kid, script);
        loop(kid, kidlang);
      }
    }
  };
  loop(node, cur_lang);
  return node;
};
const findTextNode = function(node, last = false) {
  if (node.nodeType === 3)
    return node;
  const walker = document.createTreeWalker(node, NodeFilter.SHOW_TEXT, null, false);
  if (!last)
    return walker.nextNode;
  else {
    let txt;
    while (walker.nextNode())
      txt = walker.currentNode;
    return txt;
  }
};
const jiggle = function(node, script) {
  if (node.firstChild.nodeType !== 3 && node.lastChild.nodeType !== 3)
    return;
  const kids = node.childNodes;
  const starts_with_vowel = /^[a─Бi─лu┼лeoс╣Ыс╣Эс╕╖с╣Гс╕е]/;
  const ends_with_consonant = /[kgс╣Еcj├▒с╣нс╕Нс╣Зtdnpbmyrlvс╣г┼Ыsh]$/;
  const telugu_vowels = ["─Б", "i", "─л", "e", "o", "_", "ai", "au"];
  const telu_cons_headstroke = ["h", "k", "┼Ы", "y", "g", "gh", "c", "ch", "jh", "с╣нh", "с╕Н", "с╕Нh", "t", "th", "d", "dh", "n", "p", "ph", "bh", "m", "r", "с╕╗", "v", "с╣г", "s"];
  var telugu_del_headstroke = false;
  var telugu_kids = [];
  var add_at_beginning = [];
  const starts_with_text = kids[0].nodeType === 3;
  for (let kid of kids) {
    if (kid.nodeType > 3)
      continue;
    const txt = kid.textContent.trim();
    if (txt === "")
      continue;
    if (txt === "a") {
      kid.textContent = "";
      continue;
    }
    if (txt.match(ends_with_consonant)) {
      const last_txt = findTextNode(kid, true);
      last_txt.textContent = last_txt.textContent.replace(/\s+$/, "") + "a";
      if (script === "telugu" && telu_cons_headstroke.indexOf(txt) >= 0) {
        telugu_kids.push(kid);
      }
    }
    if (kid === node.lastChild && kid.nodeType === 3) {
      const cap = document.createElement("span");
      cap.appendChild(kid.cloneNode(false));
      node.replaceChild(cap, kid);
      kid = cap;
      kid.classList.add("aalt");
    } else if (starts_with_text && txt.match(starts_with_vowel))
      kid.classList.add("aalt");
    switch (script) {
      case "devanagari":
        if (txt === "i" || txt === "├й")
          add_at_beginning.unshift(kid);
        break;
      case "grantha":
      case "malayalam":
        if (txt === "e" || txt === "ai")
          add_at_beginning.unshift(kid);
        else if (txt === "o") {
          const new_e = kid.cloneNode(true);
          replaceTextInNode("o", "e", new_e);
          add_at_beginning.unshift(new_e);
          replaceTextInNode("o", "─Б", kid);
        }
        break;
      case "telugu":
        if (!telugu_del_headstroke && telugu_vowels.indexOf(txt) >= 0)
          telugu_del_headstroke = true;
        break;
    }
  }
  for (const el of add_at_beginning) {
    node.insertBefore(el, node.firstChild);
  }
  if (telugu_del_headstroke) {
    for (const el of telugu_kids) {
      const lasttxtnode = findTextNode(el, true);
      lasttxtnode.textContent = lasttxtnode.textContent + "тАНр▒Н";
    }
  }
};
const replaceTextInNode = function(text, replace, node) {
  const walker = document.createTreeWalker(node, NodeFilter.SHOW_TEXT, null, false);
  while (walker.nextNode()) {
    let cur_txt = walker.currentNode.textContent;
    if (cur_txt.match(text))
      walker.currentNode.textContent = replace;
  }
};
export {to, changeScript};
